.button {
  width: 165px;
  height: 60px;
  margin: 170px auto;
  margin-bottom: 1200px;

  &__btn {
    width: 165px;
    height: 60px;
    border: 1px solid #626262;
    background-color: #ffffff;
    @extend %PlayfairDisplay-Regular;
    font-size: 20px;
    color: #626262;
    cursor: pointer;
    transition: all 0.5s ease;

  &:hover {
    border: 1px solid #333333;
    transform: scale(1.1);
  }
}
}
